
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import XCircle from 'vux/src/components/x-circle/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Popup from 'vux/src/components/popup/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

import { mapActions, mapState, mapMutations, mapGetters } from 'vuex';

export default {
name: 'playbar',
data() {
return {
msg: 'Hello World!',
percent: 0,
playOne: {
album: {},
alias: [],
artists: []
},
modelShow: false,
vnodeshow: true,
}
},
components: {
Flexbox,
FlexboxItem,
XCircle,
Group,
Cell,
Popup,

},
directives: {
TransferDom
},
computed: {
...mapState([
'song_catalogue',
'audiodata',
]),
...mapGetters([
'playidURL',
'playdatasing',
]),
modelShows: {
get() {
if (this.song_catalogue.length == 0) return false;
return this.modelShow
},
set() {

}
}
},
created() {
},
methods: {
...mapActions([
'recommendapi',
]),
...mapMutations([
'audiodatafn',
'modify_songCatalogue',
'changePlaylistfn',
'playingfn'
]),
playlistfn() {
this.modelShow = !this.modelShow
},
closePlaylist(index, id) {
this.modify_songCatalogue({ index: index, id: id })
},
routelink() {
this.$router.push('/detail')
},
playing() {
let _this = this;
let ading = this.$refs.ading;
if (ading.paused) {
this.audiodatafn({ playing: false })
ading.play();
} else {
this.audiodatafn({ playing: true })
ading.pause();
}









},
popuphide() {
this.modelShow = false;
},
transTime(time) {
var duration = parseInt(time);
var minute = parseInt(duration / 60);
var sec = duration % 60 + "";
var isM0 = ":";
if (minute == 0) {
minute = "00";
} else if (minute < 10) {
minute = "0" + minute;
}
if (sec.length == 1) sec = "0" + sec
return minute + isM0 + sec;
},
playfn(item) {
this.playingfn({ playing: true, id: item.id, type: 'popup' })
},
routefn() {
let path = this.$route.path;
if (path == "/detail" || path == '/fm') {
this.vnodeshow = false;
} else {
this.vnodeshow = true;
}
}
},
watch: {
'$route': 'routefn'
}
}



