export default {
  name: "home",
  data() {
    return {
      phone: '',
      password: '',
    }
  },
  created() {


  },
  methods: {
    loginIn() {
      let _this = this;
      this.$http.get('/login/cellphone', {
        params: {
          phone: _this.phone,
          password: _this.password
        },
        withCredentials: true
      }).then(res => {
        console.log(res.data);
        this.$router.push('/')
      })
    }
  }
}



