
import CellBox from 'vux/src/components/cell-box/index.vue'
import Group from 'vux/src/components/group/index.vue'

import { mapState, mapMutations, mapGetters} from 'vuex';

export default {
name: 'list',
data() {
return {
show: false,
isplay: {
id: '',
bool: false
}
}
},
props: {
datas: {
type: Array,
default: function () {
return [{}]
}
}
},
components: {
Group,
CellBox
},
computed: {
...mapState([
'audiodata'
]),
...mapGetters([
'playdatasing'
])
},
created(){
},
methods: {
...mapMutations([
'playingfn',
]),
playfn(item, index) {
this.playingfn({playing: true, id: item.id})
},
detailsfn() {
this.show = true;
}
}
}



